<template>
  <div id="detail">
    <DetailNavBar
      class="detail-nav"
      @toTitle="toTitle"
      ref="nav"
    ></DetailNavBar>
    <Scroll
      class="wrapperSize"
      ref="scroll"
      :probeType="3"
      :pullUpLoad="true"
      @scrollon="contentScroll"
      :probe-typ="3"
    >
      <DetailSwiper :topImages="topImages"></DetailSwiper>
      <DetailBaseInfo :goods="goods"></DetailBaseInfo>
      <DetailShopInfo :shop="shop"></DetailShopInfo>
      <DetailParamInfo
        :param-info="paramInfo"
        ref="paramInfo"
      ></DetailParamInfo>
      <DetailCommentInfo
        :comment-info="commentInfo"
        ref="commentInfo"
      ></DetailCommentInfo>
      <DetailGoodsInfo
        :detail-info="detailInfo"
        @imageLoad="imageRefresh"
      ></DetailGoodsInfo>
      <div class="recommendTitle" ref="recommendTitle"></div>
      <GoodList :goods="recommends"></GoodList>
    </Scroll>
    <BackTop @click.native="backClick" v-show="isShowBackTop"></BackTop>
    <DetailBottomBar @addToCart="addToCart"></DetailBottomBar>
  </div>
</template>

<script>
// 
import DetailNavBar from 'views/detail/childComp/DetailNavBar'
import DetailSwiper from 'views/detail/childComp/DetailSwiper'
import DetailBaseInfo from 'views/detail/childComp/DetailBaseInfo'
import DetailShopInfo from 'views/detail/childComp/DetailShopInfo'
import DetailGoodsInfo from 'views/detail/childComp/DetailGoodsInfo'
import DetailParamInfo from 'views/detail/childComp/DetailParamInfo'
import DetailCommentInfo from 'views/detail/childComp/DetailCommentInfo'
import DetailBottomBar from 'views/detail/childComp/DetailBottomBar'

// 
import Scroll from 'components/common/scroll/Scroll'

//
import GoodList from 'components/content/goods/GoodList'

// js
import { debounce } from 'common/utils'

// api
import { getDetail, Goods, Shop, GoodsParam, getRecommend } from 'api/detail'

// 
import { backTop } from 'common/mixin'

import { mapActions } from 'vuex'

export default {
  name: 'Detail',
  mixins: [backTop],
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    Scroll,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    GoodList,
    DetailBottomBar
  },
  data() {
    return {
      id: null,
      topImages: [],
      goods: {},
      shop: {},
      detailInfo: {},
      paramInfo: {},
      commentInfo: {},
      recommends: [],
      titleTopY: [],
      getTitleTopY: null,
      currentIndex: 0
    }
  },
  created() {
    // id
    this.id = this.$route.params.id
    //id
    this.getDetailById()
    // 
    this.getRecommend()
  },
  methods: {
    ...mapActions(['addCart']),
    /**
     *
     */
    getDetailById() {
      getDetail(this.id).then(res => {
        const data = res.data.result
        this.topImages = data.itemInfo.topImages

        this.goods = new Goods(
          data.itemInfo,
          data.columns,
          data.shopInfo.services
        )

        this.shop = new Shop(data.shopInfo)

        this.detailInfo = data.detailInfo

        this.paramInfo = new GoodsParam(
          data.itemParams.info,
          data.itemParams.rule
        )

        if (data.rate.cRate !== 0) {
          this.commentInfo = data.rate.list[0]
        }
      })
    },
    getRecommend() {
      getRecommend().then(res => {
        this.recommends = res.data.data.list
      })

      this.getTitleTopY = debounce(() => {
        this.titleTopY.push(0)
        this.titleTopY.push(this.$refs.paramInfo.$el.offsetTop - 44)
        this.titleTopY.push(this.$refs.commentInfo.$el.offsetTop - 44)
        this.titleTopY.push(this.$refs.recommendTitle.offsetTop - 44)
      }, 100)
    },
    /**
     * scroll
     */
    refresh() {
      this.$nextTick(() => {
        this.$refs.scroll.refresh()
      })
    },
    imageRefresh() {
      this.refresh()
      this.getTitleTopY()
    },
    contentScroll(positon) {
      const y = -positon.y
      const length = this.titleTopY.length
      for (let i = 0; i < length; i++) {
        if (
          //
          this.currentIndex != i &&
          // 
          ((i < length - 1 &&
            y >= this.titleTopY[i] &&
            y < this.titleTopY[i + 1]) ||
            (i == length - 1 && y >= this.titleTopY[i]))
        ) {
          this.currentIndex = i
          this.$refs.nav.currentIndex = this.currentIndex
        }
      }

      this.isShowBackTop = y > 1000 ? true : false
    },
    /**
     * 
     */
    toTitle(index) {
      this.$refs.scroll.scrollTo(0, -this.titleTopY[index], 1000)
    },
    // 
    addToCart() {
      // 1.
      const product = {}
      product.id = this.id
      product.image = this.topImages[0]
      product.title = this.goods.title
      product.desc = this.goods.desc
      product.preice = this.goods.realPrice
      product.checked = true

      // 2.
      this.addCart(product).then(res => {
        this.$toast.show(res, 2000)
      })
    }
  }
}
</script>

<style lang="scss">
$height: 44px;

#detail {
  position: relative;
  z-index: 9;
  background-color: #fff;
  //
  height: 100vh;

  .wrapperSize {
    /** 
    
    position: absolute;
    top: 44px;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #fff;
    overflow: hidden;
    */
    height: calc(100% - #{$height} - 49px);
    overflow: hidden;
  }

  // .detail-nav {
  //   position: relative;
  // }
  .recommendTitle {
    height: 40px;
    line-height: 40px;
    padding-left: 8px;
  }
}
</style>
